import './App.css';
import React, { useState, useEffect } from 'react';

import { socket, SocketContext } from './context/socket';
import {
	Routes,
	Route,
	Link,
	Outlet
} from "react-router-dom";

import HeaderComponent from './components/comman/header.component';
import Client from './components/pages/Client';
import Dashboard from './components/pages/Dashboard';

function App() {
	const [pages, setPages] = useState({ home: true, client: null, dashboard: null })

	const handlePage = (event) => {
		console.log('page event', event.target.value)
		let page = event.target.value;
		setPages({ home: null, client: null, dashboard: null })
		setPages({ [page]: true })
	}

	return (
		<SocketContext.Provider value={socket}>

			<div className="container">
				<HeaderComponent />
				<div className="actions">
					<button className="nav_button" value="home" onClick={handlePage}>Home</button>
					<button className="nav_button" value="client" onClick={handlePage}>Client</button>
					<button className="nav_button" value="dashboard" onClick={handlePage}>Dashboard</button>
				</div>	
				{/* <Outlet /> */}

				{pages.home && <div className="headline">Hellow World Home</div>}
				{pages.client && <Client />}
				{pages.dashboard && <Dashboard />}
			</div>
		</SocketContext.Provider>


	);
}

export default App;
