import React, { useContext, useEffect, useState } from 'react';
import { SocketContext } from '../../context/socket';


export default function Client() {
  const socket = useContext(SocketContext);
  const [currInterval, setCurrInterval] = useState(null)
  const [location, setLocation] = useState(null)
  // const [isAutoLocation, seIsAutoLocation] = useState(null)

  useEffect(() => {
    const interval = setInterval(() => {
      console.log('This will run every second!');
      getLocation()
    }, 1000);
    setCurrInterval(interval);
    return () => clearInterval(interval);
  }, []);


  useEffect(() => {
    // console.log('currInterval',currInterval)
  }, [currInterval]);

  useEffect(() => {
    if (location === null) {
      getLocation();
    }
  }, [location]);

  useEffect(() => {
    console.log('emit location', location)
    socket.emit('send location', location);
  }, [location]);


  const toggleInterval = async () => {
    console.log('currInterval', currInterval)
    if (currInterval) {
      clearInterval(currInterval)
      setCurrInterval(null)
    } else {
      const interval = setInterval(() => {
        console.log('This will run every second! 2');
        getLocation()
      }, 6000);
      setCurrInterval(interval);
      return () => clearInterval(interval);
    }
  }

  const getLocation = async () => {
    await navigator.geolocation.getCurrentPosition(loc => {
      // console.log("Latitude is :", location.coords.latitude);
      // console.log("Longitude is :", location.coords.longitude);
      setLocation({
        lat: loc.coords.latitude,
        lng: loc.coords.longitude
      });
      return loc
      // socket.emit('send location', loc);
    });
  }

  return (
    <div>
      <p className="headline">Hellow World Client</p>
      <div className="actions">
        <button className="button" onClick={toggleInterval}>Auto Update Location {currInterval ? ' on' : 'off'}</button>
        <button className="button" onClick={getLocation}>Menual Update Location</button>
      </div>
      {location && <div>
        <p><span className="bold">Lat: </span> <span>{location.lat}  </span></p>
        <p><span className="bold">Lng: </span> <span>{location.lng}  </span></p>
      </div>}

    </div>
  );
}